@import '../../../index.scss';
.tab-list {
  // width: 25%;
  padding-left: 0;
  white-space: nowrap;
  overflow-x: scroll;
  display: flex;
  justify-content: space-evenly;
  &::-webkit-scrollbar {
    display: none;
  }
}

.tab-list-item {
  display: inline-block;
  list-style: none;
  margin-bottom: -1px;
  padding: 0.5rem 0.75rem;
  white-space: nowrap;
  user-select: none;
  width: 100%;
}

.tab-list-active {
  // width: 25%;
  background-color: $PrimaryBgColor;
  border-width: 1px 1px 3px 1px;
  color: #fff;
}
