@import '../../../../index.scss';

$tablistHeight: 32px;
$DomainTabsHeight: 32px;

.ActiveEventInstances {
  .Selection-panel {
    .tab-list {
      height: $tablistHeight;
    }
    .event-edit {
      > .Tabs {
        height: $DomainTabsHeight;
      }
    }
  }
}

.config {
  height: calc(100vh - (60px + 22px + #{$tablistHeight + $DomainTabsHeight}));
  overflow-y: scroll;

  display: flex;

  .update-event-config {
    display: flex;
    flex: 0.6;
    flex-direction: column;
    min-width: 60%;
    box-shadow: 2px 2px 7px -3px grey;
    //  height: 80vh;
    overflow: scroll;
    padding-bottom: 40px;
    .event-signals,
    .event-signal-value {
      flex: 1;
      text-align: left;
      padding-left: 15px;
      padding-top: 15px;
    }

    .event-signals {
      flex: 1.2;
      min-width: 60%;
      .tabs .tab .slider-header {
        color: #000;
        padding-bottom: 8px;
        border-bottom: 0.5px solid #80808036;
        .tab-label {
          border: none;
          .selected-signal {
            font-size: 12px;
            padding-top: 5px;
            line-height: 15px;
            color: #000;
            opacity: 0.5;
          }
        }
      }
      .active-slide {
        .slider-header {
          background-color: $PrimaryActiveBg;
          // border-bottom: solid 0.5px #ededed;
        }
      }

      .my-dropdown-slidedown {
        padding-left: 15px;
        .tab-content {
          border: none;
          padding: 0;
        }
        .signal-attribute {
          div {
            border-bottom: 0.5px solid #80808036;
            white-space: nowrap;
            overflow: hidden;
            padding: 5px;
            h3 {
              font-size: 13px;
              color: #000;
            }
            p {
              font-size: 12px;
              color: #666666;
            }
            &.active {
              background-color: $PrimaryActiveBg;
              border-right: 2px solid #4a51c3;
            }
          }
        }
      }
    }
  }
  .event-signal-value {
    // flex: 0.4;
    text-align: left;
    padding-left: 15px;
    padding-top: 15px;
    // overflow-y: scroll;
    margin-bottom: 40px;

    .tabs .tab {
      .slider {
        .slider-header {
          .tab-label {
            border: none;
            // border-bottom: solid 0.5px #ededed;
          }
        }
        .mapping-signal {
          padding-left: 15px;
          .tab-content {
            border: none;
            .signal {
              padding: 5px 0;
            }
          }
        }
      }
    }

    .Scheduling {
      .scheduling-condition {
        color: #666666;
        .condition {
          padding: 5px 2px;
          background-color: #f8f8f8;
          font-size: 14px;
          border: solid 1px #ededed;
          font-weight: bold;
        }
        .Operator {
          padding-top: 10px;
          .dd-wrapper {
            color: #000;

            .dd-header-title-bold {
              font-weight: normal;
            }
          }
        }
        .Operator-title,
        .snooze-title,
        .incremental-title {
          font-size: 12px;
        }
        .schedule-time {
          display: flex;
          padding-top: 10px;
          .scheduletimeinput {
            position: relative;
            .minute {
              position: absolute;
              right: 15px;
              top: 50%;
              transform: translateY(-50%);
            }
          }
          .snooze,
          .incremental {
            padding-right: 5px;
            .input-style {
              height: auto;
            }
          }
        }
      }
    }
  }
}
