@import '../../../index.scss';

// header
$EventManagementMain: 65%;
$EventManagementManage: 35%;
$headerHeight: 26px;
$headerStartPosition: 21px;
.tick-mapping {
  position: absolute;
  right: 15px;
  font-weight: bold;
  top: 51%;
  transform: translateY(-50%);
  color: #2b6cce;
  font-size: 18px;
}
.EventManagementHeader {
  margin: 10px 0;
  margin-top: $headerStartPosition;
  width: 100%;
  height: $headerHeight;
  overflow: hidden;
}
.Events {
  width: 33%;
  height: 60px;
  overflow: hidden;
}
.config-container {
  height: calc(100% - 60px);

  .PopupMax-container {
    .PopupMax-title {
      height: 20px;
    }
    height: 70%;
    .PopupMax-message {
      height: calc(100% - 78px - 20px);
      .main {
        margin: 0;
        .choose-enterprise-list-container {
          width: 100%;
          height: 100%;
          top: 0;
          transform: translateY(0);
        }
      }
    }
  }
}

.EventManagementwrap {
  display: flex;
}
.EventManagementText {
  color: #666666;
  opacity: 0.7;
  font-size: 14px;
  flex: 1;
  height: 26px;
  line-height: 26px;
  padding-left: 10px;
  cursor: pointer;
}
.Event-header-right-content {
  flex: 1.1;
  display: flex;
  justify-content: flex-end;
}

.event-search-header {
  position: relative;
  .event-search-icon {
    position: absolute;
    width: 18px;
    height: $headerHeight;
    line-height: $headerHeight;
    color: #999999;
    left: 7px;
  }
  .EventManagementSearchBar {
    width: 100%;
    border-radius: 10px;
    border: solid 1px #f0f0f0;
    background-color: #ffffff;
    height: 26px;
    font-family: Roboto;
    font-size: 12px;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.17;
    letter-spacing: normal;
    text-align: left;
    color: #999999;
    padding-left: 28px;
    border-radius: 10px;
    border: solid 1px #f0f0f0;
    background-color: #ffffff;
    height: $headerHeight;
    font-family: Roboto;
    font-size: 12px;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.17;
    letter-spacing: normal;
    text-align: left;
    color: #999999;
    padding-left: 28px;
  }
}
.event-header-create {
  position: relative;
  width: 65%;
  .event-create-icon {
    position: absolute;
    width: 11px;
    height: 26px;
    line-height: 26px;
    font-weight: bold;
    color: #fff;
    left: 11px;
    font-size: 12px;
  }
  .CreateEventTemplate {
    width: 100%;
    border-radius: 20px;
    margin-left: 0px;
    color: #ffffff;
    background-color: #4b50c3;
    height: 26px;
    font-size: 12px;
    font-family: 'Roboto';
    font-stretch: normal;
    font-style: normal;
    line-height: 23px;
    letter-spacing: normal;
    text-align: left;
    padding-left: 23px;
    outline: none;
    white-space: nowrap;
    overflow: hidden;
  }
  .Searchbartesxtbox {
    width: 108px;
    border-radius: 20px;
    margin-left: 5px;
    color: #ffffff;
    background-color: #4b50c3;
    height: 26px;
    font-size: 12px;
    font-family: 'Roboto';
    font-stretch: normal;
    font-style: normal;
    line-height: 26px-3;
    letter-spacing: normal;
    text-align: left;
    padding-left: 28px;
  }
}

//enterprise select

.main {
  .choose-enterprise-heading {
    color: #c3c3c3;
    top: 10%;
    text-align: center;
    position: relative;
  }
  .choose-enterprise-list-container {
    display: flex;
    position: relative;
    margin: auto;
    width: 40%;
    max-height: 70vh;
    overflow: scroll;
    flex-direction: column;
    top: 50%;
    transform: translateY(-50%);
    background-color: #fff;
    border-radius: 12px;
    box-shadow: 0 1.5px 3px 0 rgba(0, 0, 0, 0.16);
    border: solid 0.5px #ededed;

    .list-enterprise {
      width: 100%;
      margin-bottom: 2px;
      display: flex;
      padding: 15px;
      cursor: pointer;
      border-bottom: solid 0.5px #ededed;
      &:hover {
        background-color: $PrimaryActiveBg;
      }
    }
    .enterprise-logo {
      flex: 0.2;
      justify-content: center;
      .logo-img {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #ededed;
        border: solid 0.5px #c3c3c3;
        color: #c3c3c3;
        font-weight: bold;
      }
    }
    .enterprise-details {
      flex: 0.8;
      .id-name-container {
        display: flex;
        font-size: 14px;
        line-height: 20px;
        opacity: 0.8;
        .enterprise-id {
          flex: 0.5;
          text-align: left;
        }
        .enterprise-name {
          flex: 0.5;
        }
      }
      .enterprise-desc {
        font-size: 12px;
        color: #666666;
        line-height: 20px;
      }
    }
  }
}
