@import '../../../../index.scss';
.mapping-parent {
  .slider {
    padding-right: 0;
    padding-top: 1px;
    .slider-header {
      padding: 5px;
      color: #000;
      font-size: 14px;
      font-weight: bold;
      padding: 10px;
      padding-left: 5px;
      div:first-child {
        margin-left: 10px;
      }

      div {
        padding-bottom: 5px;
        border-bottom: 0.8px solid #80808036;
      }
    }
    &.active-slide {
      > .slider-header {
        background-color: $PrimaryActiveBg;

        div {
          border-bottom: transparent;
        }
      }
      .last-child-mapper {
        padding-left: 20px;
      }
    }
    .active-mapping {
      background-color: $PrimaryActiveBg;
      margin-top: 1px;
    }
  }
  > .mappingslider {
    > .slider {
      > .slider-header {
        div {
          margin-left: 0px;
        }
      }
    }
  }

  .last-child-mapper {
    padding: 5px;
    padding-right: 0;
    position: relative;
    cursor: pointer;
    color: #000;
    font-size: 14px;
    font-weight: bold;
    div {
      border-bottom: 0.8px solid #80808036;
      padding-top: 5px;
      padding-bottom: 5px;
    }

    &.active-mapping {
      div {
        border-bottom: transparent;
      }
    }

    .mapping-tick {
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 51%;
      transform: translateY(-50%);
      color: #2b6cce;
      font-size: 18px;
    }
  }
  .mappingslider {
    position: relative;
    .openIcon {
      position: absolute;
      right: 5px;
      top: 8px;
      // transform: translateY(-50%);
    }
  }
}

.error-text-mapping {
  font-size: 14px;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  color: $PrimaryTextColor;
}
