$clouds: #ecf0f1;
// Layout
.row {
  display: flex;
}
/* Accordion styles */
.tabs {
  width: 100%;
  overflow: hidden;
}
.tab {
  width: 100%;
  color: #666666;
  overflow: hidden;
  .slider {
    border-bottom: solid 0.5px #ededed;
    .minus {
      display: none;
    }
  }
  &-label {
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    font-weight: 500;
    align-items: center;
    padding: 0px 18px;
    border-bottom: solid 0.5px #ededed;
    cursor: pointer;
    // border-radius: 10px;
    padding: 12px 19.5px 12px 18px;
  }
  &-content {
    padding: 0 1em;
    color: #000000;
    // border: solid 1px #d9d9d9;
    transition: all 0.35s;
  }
  &-close {
    display: flex;
    justify-content: flex-end;
    padding: 1em;
    font-size: 0.75em;

    cursor: pointer;
  }
}

//active style

.tab {
  .active-slide {
    .tab-label {
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
    .add {
      display: none;
    }
    .minus {
      display: block;
    }
  }
}

// :checked
.heading {
  + .tab-label {
    // border: solid 1px #d9d9d9;
    // border-top-left-radius: solid 1px #d9d9d9;
    // border-top-right-radius: solid 1px #d9d9d9;
    // background-color: #f8f8f8;
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    font-weight: 500;
    align-items: center;
    padding: 0px 18px;
    border-bottom: solid 1px #d9d9d9;
    cursor: pointer;
    /* border-radius: 10px; */
    padding: 12px 19.5px 12px 18px;

    &::after {
      transform: rotate(90deg);
      // border-bottom-left-radius: 0px;
      // border-bottom-right-radius: 0px;
    }
  }
  ~ .tab-content {
    /* padding: 0 1em; */
    color: #000000;
    /* border: solid 1px #d9d9d9; */
    transition: all 0.35s;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(-10%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
    height: 100px;
  }
  100% {
    opacity: 0;
    height: 0px;
    margin-top: -2px;
  }
}
