@import '../../../../index.scss';

.tab-list {
  background-color: #fff;

  border: 0.5px solid #80808036;
}

.event-edit {
  &.tab-list-item {
    color: $PrimaryTextColor;
    cursor: pointer;

    border-left: 0.5px solid #80808036;
    border-right: 0;
    margin-bottom: 0;
    font-size: 13px;
    text-align: center;
  }

  &.tab-list-item:last-child {
    border-right: 0.3px;
  }

  &.tab-list-active {
    color: #fff;
    background-color: $PrimaryBgColor;
  }

  .Tabs__Tab {
    background-color: #fff;
    color: #666666;
    text-align: center;
    padding: 8px;
    font-weight: bold;
    font-size: 13px;

    &.Tabs__Tab--active {
      color: $PrimaryTextColor;
    }
  }
  .save-container {
    position: fixed;
    bottom: 0;
    right: 0;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    padding: 10px;
    width: 67%;
    height: 42px;
    background: #f8f8f8;
    button {
      font-size: 14px;
    }
    button:last-child {
      background-color: $PrimaryBgColor;
      color: #fff;
    }
  }

  &[label='Subscription'] {
    & > .Tabs {
      max-height: 0;
    }
  }

  .subscription-config {
    display: flex;
    padding: 0px;
    min-height: 80vh;
    .subscription-list {
      flex: 1;
      border-right: 1px solid #ededed;
      .subscription-group {
        border-bottom: 1px solid #ededed;
        text-align: left;
        padding: 15px;
        color: #000;
        font-size: 14px;
        cursor: pointer;
        &:hover {
          background-color: $PrimaryActiveBg;
        }
        &.activeInstance {
          background-color: $PrimaryActiveBg;
          border-right: 2px solid #4a51c3;
        }
      }
    }
    .subscription-period-container {
      flex: 1;
      justify-content: left;
      .Subscription-period {
        background: #f8f8f8;
        height: 36px;
        line-height: 36px;
        padding-left: 15px;
        font-size: 14px;
        color: #666666;
        text-align: left;
        font-weight: bold;
        margin-bottom: 5px;
      }

      .start,
      .end,
      .enable-subscription {
        padding-left: 15px;
        .title {
          font-size: 12px;
          text-align: left;
          //font-weight: bold;
          padding-top: 5px;
          color: #666666;
        }
        .date-picker-parent {
          text-align: left;
          padding: 5px;
          padding-left: 0;
          font-weight: bold;
          color: #000;
          position: relative;
          width: 70%;
          .calendar-icon {
            position: absolute;
            top: 50%;
            right: 15px;
            font-size: 20px;
            line-height: 20px;
            color: #666666;
            transform: translateY(-50%);
          }
          .react-datepicker-wrapper {
            display: flex;
          }
          input {
            height: 36px;
            border-radius: 5px;
            font-size: 13px;
            width: 100%;
            border: solid 1px #d3d3d3;
            padding-left: 10px;
            color: #000;
          }
        }
        .date-picker {
          font-weight: bold;
          text-align: left;
        }
        .toggle-subscription {
          padding: 5px;
          padding-left: 0;
          .Toggle {
            height: 34px;
            width: 110px;
          }
        }
      }
    }
  }
}

.eventListView {
  width: 100%;
  color: #666666;
  text-align: left;

  .userCreateMain {
    display: flex;
    color: #666666;
    padding: 13px 25px 12.5px 17px;
    background-color: #ffffff;

    .userCreateName {
      // text-align: left;
      width: 95%;
    }

    .edit {
      color: #4b50c3;
      cursor: pointer;
    }
  }

  .bottomline {
    width: 100%;
    border: solid 1px #ededed;
  }

  .eventListViewDescription {
    margin: 10.5px 25px 0px 27px;

    .update-event-config {
      margin-bottom: 0;
      flex: 0.6;
    }

    .businesseventname {
      border-radius: 10px;
      border: solid 1px #f0f0f0;
      background-color: #ffffff;
      padding: 10px 12px 14px 10px;

      .businesseventdetails {
        color: #000000;
        display: flex;
        // text-align: left;
        font-size: 14px;
        font-weight: 500;

        .eventNo {
          width: 20%;
        }

        .eventName {
          width: 60%;
        }
      }

      .businesseventdescription {
        font-size: 12px;
        margin: 8px 48px 0px 0px;
        text-align: left;
      }
    }

    .Domain {
      color: #000000;

      .header {
        opacity: 0.5;
        margin: 20px 0px 10px 0px;
        font-size: 12px;
        font-weight: bolder;
      }

      .domainDetails {
        border-radius: 10px;
        border: solid 1px #f0f0f0;
        background-color: #ffffff;
        font-size: 10pt;
        font-weight: 500;
        overflow: hidden;
        .domainNames {
          border-bottom: solid 1px #f0f0f0;
          padding: 10px 12px 12px 10px;

          .domainNames:last-child {
            border-bottom: none;
          }
        }

        .line {
          border: solid 1px #ededed;
          background-color: #f8f8f8;
        }
      }
    }
  }
}
