@import '../../../../index.scss';

$TurnManagementMain: 100%;
.ActiveEventInstances {
  height: 100%;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  overflow: hidden;
}

.Event_Box {
  width: 100%;
  background-color: #f8f8f8;
  border: solid 1px #e7e8f7;
  /* border-radius: 10px; */
  border-top-left-radius: 10px;
  /* border-top-right-radius: 10px; */
  display: flex;
  /* margin-bottom: 5px; */
  margin-top: 0px;
  padding: 8px;
}

.searchContainer {
  width: 100%;
  /* border-bottom: solid 1px #e7e8f7; */
  background-color: #f8f8f8;
}
.Event-search-header {
  position: relative;
  background-color: #f8f8f8;
  .Event-search-icon {
    position: absolute;
    width: 16px;
    height: 26px;
    line-height: 26px;
    color: #999999;
    left: 7px;
  }
  .TurnManagementSearchBar {
    // position:absolute;
    width: 100%;
    border-radius: 10px;
    border: solid 1px #f0f0f0;
    background-color: #ffffff;
    height: 26px;
    font-family: Roboto;
    font-size: 12px;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.17;
    letter-spacing: normal;
    text-align: left;
    color: #999999;
    padding-left: 28px;
  }
  .EventListSearchBar {
    width: 100%;
    border-radius: 10px;
    border: solid 1px #f0f0f0;
    background-color: #ffffff;
    height: 26px;
    font-family: Roboto;
    font-size: 12px;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.17;
    letter-spacing: normal;
    text-align: left;
    color: #999999;
    padding-left: 28px;
    border-radius: 10px;
    border: solid 1px #f0f0f0;
    background-color: #ffffff;
    height: 26px;
    font-family: Roboto;
    font-size: 12px;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.17;
    letter-spacing: normal;
    text-align: left;
    color: #999999;
    padding-left: 28px;
  }
}
.EventListBox {
  // width: 100%;
  display: flex;
  flex-direction: column;
  height: calc(100% - 43px);
  overflow-y: scroll;
  background-color: #ffff;
  border: solid 1px #e7e8f7;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  /* border-radius: 10px; */

  .EventListHeaderBox {
    width: 100%;
    height: 37px;
    background-color: #ffff;
    /* border: solid 1px #e7e8f7; */
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    /* border-radius: 10px; */
    display: flex;
  }

  .EventListHeader {
    width: 100%;
    height: 25px;
    background-color: #ffff;
    font-family: Roboto;
    font-size: 14px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.14;
    letter-spacing: normal;

    text-align: center;
    color: #666666;
    margin-top: 10px;
    border-bottom: 1px solid #ededed;
  }
  .EventListHeaderdraft {
    width: 100%;
    height: 25px;
    background-color: #ffff;
    font-family: Roboto;
    font-weight: 500;
    font-size: 14px;
    text-align: left;
    color: #666666;
    margin-top: 6px;
    border-bottom: 1px solid #ededed;
  }
}
.Event-left-box {
  height: 100%;
}
.EventListDetails {
  display: flex;
  flex-direction: column;
  height: auto;
  background-color: #ffff;
  font-family: Roboto;
  font-size: 14px;
  /* font-weight: 500; */
  /* font-stretch: normal; */
  /* font-style: normal; */
  /* line-height: 1.14; */
  padding-bottom: 25%;
  /* letter-spacing: normal; */
  border-bottom: solid 1px #ededed;
  cursor: pointer;
  white-space: nowrap;
  text-overflow: ellipsis;
  border-right: 2px solid transparent;
  &.activeInstance {
    background-color: $PrimaryActiveBg;
    border-right: 2px solid #4a51c3;
  }
  &:hover {
    background-color: $PrimaryActiveBg;
  }
  .EventListName {
    // height: 14px;
    font-family: Roboto;
    /* height: 17px; */
    font-size: 12px;
    /* font-weight: 500; */
    margin-top: 10px;
    font-weight: bold;
    /* font-stretch: normal; */
    /* font-style: normal; */
    /* line-height: 3.5; */
    /* letter-spacing: normal; */
    text-align: left;
    color: #666666;
    margin-left: 14px;
  }
  .EventListUserName {
    font-family: Roboto;
    /* height: 14px; */
    font-size: 12px;
    margin-top: 5px;
    /* font-weight: normal; */
    /* font-stretch: normal; */
    /* font-style: normal; */
    /* line-height: 4.5; */
    /* letter-spacing: normal; */
    text-align: left;
    color: #000000;
    margin-left: 14px;
  }
  .EventListBEventName {
    font-family: Roboto;
    font-size: 11px;
    margin-top: 5px;
    /* font-weight: normal; */
    /* font-stretch: normal; */
    /* font-style: normal; */
    /* line-height: 6.5; */
    /* letter-spacing: normal; */
    text-align: left;
    color: #666666;
    margin-left: 14px;
  }

  .EventListULName {
    display: flex;
    padding: 5px;
    padding-bottom: 8px;
    flex-direction: row;
    /* height: 11px; */
    font-family: Roboto;
    font-size: 10px;
    margin-top: 2px;
    padding-left: 0;
    /* font-weight: normal; */
    /* font-stretch: normal; */
    /* font-style: normal; */
    /* line-height: 8; */
    /* letter-spacing: normal; */
    text-align: left;
    color: #666666;
    // margin-left: 14px;
    .Event-Transactional-button-Area {
      flex: 1;
      display: flex;
      justify-content: flex-start;
      .Event-Transactional-button {
        width: 75px;
        height: 16px;
        font-family: Roboto;
        border: solid 1px #e2e2e2;
        font-size: 8px;
        /* font-weight: 500; */
        font-stretch: normal;
        font-style: normal;
        /* line-height: 4.29; */
        letter-spacing: 0.35px;
        color: #909090;
        background-color: #f8f8f8;
        outline: none;
        text-transform: uppercase;
      }
    }
  }
}
.ActiveEventInstances {
  width: 100%;
  display: flex;
  flex-direction: row;
  // width: 256px;
  height: 100%;
  font-family: Roboto;
  font-size: 16px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.17;
  letter-spacing: normal;
  text-align: center;
  color: #c3c3c3;
  backdrop-filter: blur(30px);
  background-color: #f8f8f8;

  .event-list {
    width: 33%;
    height: 100%;
    box-shadow: -2px 0px 9px 0px grey;
    position: relative;
    z-index: 1;
  }

  .Selection-panel {
    width: 67%;
    border-top-right-radius: 4px;
    background-color: #fff;
    height: 100%;
    overflow-y: scroll;
  }
  .Selection-image {
    display: flex;
    min-height: 90vh;
    justify-content: center;
    align-items: center;
  }
}

.Flights_Details {
  width: 65%;
  display: flex;
  font-size: 14px;
}
.Flight_Number {
  width: 45%;
  color: $PrimaryTextColor;
  font-size: 14px;
}
.BaseBody {
  font-size: 12px;
  color: $SecondaryTextColor;
  padding-top: 8px;
}

.Base_Details {
  width: 65%;
  color: #000000;
  font-weight: bold;
}

._Date_Time {
  display: flex;
  width: 45%;
  color: $SecondaryTextColor;
}
._Date1 {
  width: 50%;
  display: flex;
  justify-content: flex-end;
}
._Time1 {
  margin-left: 10px;
}
.no-event {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
